*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

ul {
    list-style-type: none;
}

.area-bg {
    background: rgba(0, 0, 0, 0.6) url('./imagini/bg-image.jpg');
    min-height: 90vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-blend-mode: darken;
    
    
    
   
}

.nav-toggle {
    background-color: green;
}



.nav-item {
    color: #fff;
    font-weight: 700;
    font-size: 1.2rem;
}

.nav-item:hover {
    color: green !important;
}

.bg-home {
    margin-top: 9rem;
}


.f-title {
    color: green;
    font-weight: 800;
    font-size: 1.3rem;
    letter-spacing: .2rem;
}

.s-title {
font-size: 3rem;
font-weight: 700;
line-height: 1.3;
color: #fff;

}


.btn-prime {
   margin-right: 2rem;
   border-radius: 1rem;
   border: 1px solid transparent;
   padding: .6rem;
   background-color: green;
   color: #fff;
   font-weight: 700;
   
}

.btn-prime:hover{
    background-color: black;
}

.btn-second {
    border-radius: 1rem;
    border: 1px solid transparent;
    padding: .7rem;
    background-color: blue;
    color: #fff;
    font-weight: 700;
    
}


/* Header */


.home-bg {
    background-color: #f5f5f5;
}

.home-content h3 {
    font-weight: 700;
}

.home-content p {
    font-size: 0.9rem;
}
.row-home {
    padding: 6rem;
}

.fas {
    display: block;
font-size: 24px;
margin-right: 15px;
color: green;
width: 65px;
height: 65px;
text-align: center;
line-height: 60px;
border: 2px green dotted;
border-radius: 999px;
}

.product-title-container {
    text-align: center;
    margin: 8rem 8rem 3rem 8rem;
}

.product-title-container h3 {
    font-size: 3rem;
}


.card-item {
    width: 15rem;
    margin: 0 1.5rem;
    box-shadow: 0 0 20px #e4e4e4;
    border-radius: 1rem;
    cursor: pointer;
    border: transparent;
}

.card-title {
    font-size: 1rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.card-price {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.card-img {
    padding-top: 3rem;
}

.card-button {
    padding: .8rem;
    background-color: green;
    border-radius: 1rem;
}

.fa-cart-shopping {
    margin-right: .5rem;
}

.benefit-row {
    padding: 7rem;
}

.fa-circle-check {
    color: green;
    margin-right: .7rem;
}

.benefit-title {
    font-size: 1.6rem;
    margin-bottom: .8rem;
}

.discover-link {
    color: #36464e;
}
/* Home */

.review div:nth-child(1){
    display: inline-block;
    width: 150px;
    height: 120px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 1rem;
}

.review div:nth-child(2){
    margin-bottom: 3rem;
}

.review p{
    width: 70%;
    font-size: 1.3rem;
    font-weight: 700;
    margin: 0 auto;
}

.foo-item {
    margin-right: 5rem;
    width: 25%;
}


.review div img {
    width: 100%;
    height: 100%;
    
}


/* Footer */

Footer {
    background-color: #051922;
    color: white;
}

.last-div {
    border-top: 1px solid;
    padding: 1rem 15rem;
}

/* Footer */

